@import url(https://fonts.googleapis.com/css?family=Nova+Mono);
body {
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  height: 100vh;
  overflow: hidden;
  font-family: 'Nova Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, right bottom, left top, color-stop(-10%, rgba(7,27,82,1)), to(rgba(0,128,128,1)));
  background: linear-gradient(to top left, rgba(7,27,82,1) -10%, rgba(0,128,128,1) 100%);
}
div h1.lh-copy {
    color: #10eb85;
}
div {
    outline: none;
}

*:focus {
    outline: none;
    box-shadow: 0px 4px 7px 1px rgba(0, 0 , 0, 0.25);
    transform: scale(1.05);
    -webkit-transform: scale(1.05);
    -moz-transform: scale(1.05);
    -ms-transform: scale(1.05);
    -o-transform: scale(1.05);
}
.visuallyhidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}
div.scroll {
    overflow-y: scroll;
    border: 0.5px solid rgba(14, 241, 14, 0.185);
    height: 69vh;
    padding: 10px 5px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

/* Let's get this party started */
::-webkit-scrollbar {
    width: 10px;
}
 
/* Track */
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.6);
    border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgba(223, 88, 88, 0.8); 
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.6); 
}
::-webkit-scrollbar-thumb:window-inactive {
	background: rgba(223, 88, 88, 0.4); 
}
 
